@import 'reset/reset.css';

html {
  font-weight: bold;
  color: #666;
  padding: 30px;
  height: auto;
	lost-utility: edit;
}

header {
  text-align: center;
  margin: 30px 0 60px;
}

div {
	text-align: right;
}

div div {
  margin-top: 0;
  margin-bottom: 0;
}

section {
	lost-center: 1240px;
}

section:last-of-type {
  margin-bottom: 0;
}
section:nth-of-type(1) {
  background: #fff;
}
section:nth-of-type(1) div {
  lost-column: 1/1;
	min-width: 280px;
  background: #fff;
}
@media (min-width: 632px) {
  section:nth-of-type(1) div {
    lost-column: 1/2;
		min-width: 280px;
  }
}
@media (min-width: 936px) {
  section:nth-of-type(1) div {
    lost-column: 1/3;
		min-width: 280px;
  }
}
@media (min-width: 1204px) {
  section:nth-of-type(1) div {
    lost-column: 1/4;
		min-width: 280px;
  }
}
